.navigateList {
    width: 100%;
    display: flex;
    justify-content: center;
}

.itemLink {
    margin: 0 30px;
    position: relative;
}

.decor {
    width: 30px;
    object-fit: cover;
    position: absolute;
    top: 15px;
    left: 5px;
}

.link {
    height: 60px;
    width: 173px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    letter-spacing: 0.4px;
    opacity: 0.4;
    border-bottom: 3px solid transparent;
}

.linkActive {
    background: #DCCCDB;
    color: #2D2E46;
    opacity: 1;
    border-bottom: 3px solid #21268F;
}

.itemWrapper{
    width: 100%;
    display: flex;
    justify-content: center;
}

